import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

import styles from './header.module.css'

const Items = ["Rule", "ResourcePack"]

const LinkStyle = {
  background: 'rebeccapurple',
  color: 'white',
  fontWeight: 'normal'
}

const ActiveLinkStyle = {
  background: 'white',
  color: 'rebeccapurple',
  fontWeight: 'bold'
}

const Menu = Items.map((item) => {
  let page_link = "/" + item.toLowerCase() + "/"; 

  return (
      <li key={page_link}>
          <Link to={page_link} style={LinkStyle}
              activeStyle={ActiveLinkStyle}
              className="page-link"
          >
              {item}
          </Link>
      </li>
  )
});



const Header = ({ siteTitle }) => (
  <header
    style={{
      background: `rebeccapurple`,
      marginBottom: `1.45rem`,
    }}
  >
    <nav className={styles.navbar}>
      <h1>
          <Link
            to="/"
            style={{
              color: `white`,
              textDecoration: `none`,
            }}
          >
            {siteTitle}
          </Link>
      </h1>
      <div className={styles.navbar_item}>
        <ul>
          {Menu}
        </ul>
      </div>
    </nav>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
